@import "./../../gallery.scss";
@import "./../../info-page.scss";

.news-and-articles {
  .gallery__container{
    padding-top: 28px;
  }
  .page-banner_litle-unbackground {
    height: 224px;
    display: flex;
    justify-content: center;
    padding-top: 67px;
    @media (max-width: $tablet) {
      padding-top: 100px;
    }
    @media (max-width: $mobile) {
      padding-top: 60px;
      height: 130px;
    }
    .banner-container {
      &__title {
        @include rale-800-45;
        text-align: center;
      }
      &__description {
        @include rale-500-18;
        text-align: center;
        margin-top: 24px;
        @media (max-width: $mobile) {
          margin-top: 10px;
        }
      }
    }
  }

  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .swipers-news-section {
    padding-bottom: 90px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &__container {
      .swiper-news-block-swiper-big {
        width: 100%;
        min-width: 0;
        overflow: hidden;
        .swiper-wrapper.news-block-swiper__wrapper {
        }
        .news-block-swiper__slide {
          display: flex;
          justify-content: space-between;
          @media (max-width: $tablet) {
            flex-direction: column;
            gap: 20px;
          }
          .img-block{
            height: 100%;
          }
          img {
            max-width: 849px;
            max-height: 526px;
            width: 100%;
            height: 100%;
            min-width: 0;
            object-fit: cover;
            @media (max-width: $tablet) {
              max-width: none;
            }
          }
          .slide-text-block {
            padding: 7px 0px 27px 20px;
            height: 100%;
            max-width: 415px;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @media (max-width: $tablet) {
              padding: 0;
              padding-left: 50px;
              max-width: none;
            }
            @media (max-width: $mobile) {
              padding-left: 0;
            }
            &__date {
              margin-top: 64px;
              @include mont-500-13;
              color: $gray;
              @media (max-width: $tablet) {
                margin-top: 0;
              }
            }
            &__title {
              @include rale-700-22;
              margin-top: 20px;
              max-width: 320px;
              line-height: 27px;
              letter-spacing: 0;
              margin-bottom: 12px;
              @media (max-width: $mobile) {
                margin-top: 10px;
                margin-bottom: 0;
              }
            }
            &__description {
              @include rale-500-16;
              max-width: 500px;
              margin-top: 10px;
              line-height: 24px;
            }
            &__text-button {
              display: block;
              margin-top: 20px;
              @media (max-width: $mobile) {
                margin-top: 10px;
              }
              &::after {
                content: "→";

                margin-left: 8px;
                font-size: 21px;
              }
              &:hover {
              }
              &:active {
              }
              &:focus {
              }
            }
          }
        }
      }
      .swiper-container.news-block-swiper__swiper-news-block-swiper.swiper-news-block-swiper {
        margin-top: 60px;
        overflow: hidden;
        .swiper-wrapper.news-block-swiper__wrapper {
        }
        .swiper-slide.news-block-swiper__slide {
          background-color: $white;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: initial;
          img {
            width: 100%;
            height: 270px;
            object-fit: cover;
          }
          .slide-text-block {
            padding: 23px 23px 27px 0px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @media (max-width: $mobile) {
              max-height: 280px;
            }
            &__date {
              margin-top: 1px;
              @include mont-500-13;
              color: $gray;
            }
            &__title {
              @include rale-700-16;
              margin-top: 10px;
              max-width: 370px;
              line-height: 24px;
              @media (max-width: $mobile) {
                margin-top: 0;
              }
            }
            &__description {
              @include rale-500-14;
              max-width: 350px;
              margin-top: 9px;
              @media (max-width: $mobile) {
                margin-top: 0;
              }
            }
            &__text-button {
              display: block;
              margin-top: 20px;
              @media (max-width: $mobile) {
                margin-top: 0;
              }
              &::after {
                content: "→";

                margin-left: 8px;
                font-size: 21px;
              }
              &:hover {
              }
              &:active {
              }
              &:focus {
              }
            }
          }
        }
      }
      .rectangle.unbackground {
        margin-top: 70px;
        @media (max-width: $tablet) {
          margin-top: 30px;
        }
        @media (max-width: $mobile) {
          margin-top: 20px;
        }
        padding: 17px 37px 17px 39px;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }
  .body-contacts {
    background-color: #fbfbfb;
    padding-bottom: 90px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  .footer {
    padding-top: 45px;
    @media (max-width: $mobile) {
      padding-top: 30px;
    }
  }
}
