@import "./../components/default/default.scss";

.gallery__gallery-grid-blocks {
  display: grid;
  gap: 30px;
  @media (max-width: $tablet) {
    gap: 10px;
  }
  .grid-row-1,
  .grid-row-2,
  .grid-row-3 {
    display: grid;
    gap: 10px;
    &.reverse{
      .item-gallery-grid-blocks:nth-child(2) {
        grid-column: 1 / 1;
      }
      
      .item-gallery-grid-blocks:nth-child(1) {
        grid-column: 2 / 2;
        grid-row: 1;
      }
    }
  }

  .grid-row-1 {
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    .item-gallery-grid-blocks {
      &:nth-child(1) {
        max-width: 739px;
        max-height: 556px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
          width: 100%;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(2) {
        max-width: 520px;
        max-height: 556px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
    }
    @media (max-width: $tablet) {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .grid-row-2 {
    grid-template-columns: 1fr;
    .item-gallery-grid-blocks {
      position: relative;
      @media (max-width: $tablet) {
        max-width: none;
        height: 390px;
      }
      @media (max-width: $mobile) {
        height: 300px;
      }
    }
  }

  .grid-row-3 {
    grid-template-columns: repeat(3, auto);
    justify-content: space-between;
    .item-gallery-grid-blocks {
      &:nth-child(1) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(2) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(3) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
    }
    @media (max-width: $tablet) {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .grid-row-1 div,
  .grid-row-2 div,
  .grid-row-3 div {
    width: 100%;
    height: 100%;
  }
  .item-gallery-grid-blocks {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    &__picture-block {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .text-block,
  .video-block {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    font-family: "Montserrat";
    color: $white;
  }

  .text-block{
    background-color: rgba($color: $darkGreen, $alpha: .97);
    padding: 75px 48px 20px 39px;
    .text-block__title{
      font-size: 22px;
      font-weight: 800;
    }
    .text-block__text{
      margin-top: 18px;
      font-size: 15px;
      font-weight: 500;
      max-width: 480px;
    }
    @media (max-width: $mobile) {
      padding: 20px 20px 20px 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  .video-block {
    padding: 20px;
    background-color: rgba($color: #000000, $alpha: 0.4);
    cursor: pointer;
    &:hover {
      background: rgba(0, 0, 0, 0.55);
    }
    .icon-play {
      position: relative;
    
      border: 2px solid $white;
      border-radius: 50%;
      width: 61px;
      height: 61px;
      @media (max-width: $mobile) {
        height: 45px;
        width: 45px;
      }
      &:before {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-35%, -50%);
      }
    }

    &.video-big {
      padding-top: 18.5%;
      .text-block__title {
        margin-top: 24px;
      }
      @media (max-width: $tablet) {
        padding-top: 20px;
        justify-content: center;
      }
    }
    &.video-small {
      padding-top: 26.5%;
      .text-block__title {
        margin-top: 20px;
      }
      &.video-untext{
        padding-top: 43%;
        @media (max-width: 1200px) {
          padding-top: 20px;
          justify-content: center;
        }
      }
      @media (max-width: 1200px) {
        padding-top: 20px;
        justify-content: center;
      }
    }

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .text-block__title {
      font-size: 22px;
      font-weight: 800;
    }
    .text-block__text {
      font-size: 14px;
      font-weight: 500px;
      max-width: 563px;
      width: 100%;
      margin-top: 10px;
      strong {
        font-weight: 700;
      }
    }
  }
}

.unbackground {
  margin-top: 70px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
  @media (max-width: $tablet) {
    margin-top: 30px;
  }
  @media (max-width: $mobile) {
    margin-top: 20px;
  }
  &:hover {
  }
  &:active {
  }
  &:focus {
  }
}
